import * as React from 'react';
import {NavigationBarComponent} from '../../../common/navigation-bar-component/navigation-bar-component';
import {RoutingPass} from '../../../../constant/routing-pass';
import './page-about-component.scss';

interface PageWebAboutComponentProps {
}

interface PageWebAboutComponentState {
}

export class PageAboutComponent extends React.Component<PageWebAboutComponentProps, PageWebAboutComponentState> {

  constructor(props: PageWebAboutComponentProps) {
    super(props);
  }

  render() {
    return (
      <div className={'about_wrap'}>

        <div className={'header_wrap about_header'}>
          <NavigationBarComponent
            callback={() => this.props['history'].push(RoutingPass.HOME)}
            pageTitle={''}
          />
        </div>

        <div className={'body_area about_body'}>
          <div className={'bodyInner_wrap about_bodyInner'}>
            
            <div className={'divide20'} />
            <br />
            <br />
            <br />
            <br />
            <br />
            <br />
            
            <div className={'divide20'} />
            <br />
            
            <div className={'divide20'} />
            <br />
            <br />
            
            <div className={'divide20'} />
            <span></span>
            <div className={'divide20'} />
            
          </div>
        </div>
      </div>
    );
  }

}



