import {Station} from '../model/station';

export class StationList {
  private station: Station[];
  constructor(){
    this.station = this.stationList();
  }

  private stationList(): Station[] {
    const arr: Station[] = [
      new Station(1, 'line_cd', '', 1, 1),
      new Station(2, '11415', '', 2, 2),
      new Station(3, '11415', '', 3, 3),
      new Station(4, '11415', '', 4, 4),
      new Station(5, '11415', '', 5, 5),
      new Station(6, '11415', '', 6, 6),
      new Station(7, '11415', '', 7, 7),
      new Station(8, '11415', '', 8, 8),
      new Station(9, '11415', '', 9, 9),
      new Station(10, '11415', '', 10, 10),
    ];
    return arr;
  }

  public getList(inputValue: string): Station[] {
    const arr: Station[] = [];
    this.station.map((data) => {
      if(data.name.indexOf(inputValue) === 0){
        arr.push(data);
      }
    });
    return arr;
  }
}


