import {Route} from '../model/route';

export class RouteList {
  private route: Route[]
  constructor(){
    this.route = this.stationList();
  }

  private stationList(): Route[] {
    const arr: Route[] = [
      new Route(1, '11415', 'JR()', '1'),
      new Route(2, '11416', 'JR', '2'),
      new Route(3, '11417', 'JR', '3'),
      new Route(4, '11418', 'JR', '4'),
      new Route(5, '11419', 'JR', '5'),
      new Route(6, '11420', 'JR', '6'),
      new Route(7, '11421', 'JR', '7'),
      new Route(8, '11422', 'JR', '8'),
      new Route(9, '11423', 'JR()', '9'),
      new Route(10, '11424', 'JR', '10'),
    ];
    return arr;
  }

  public getList(inputValue: string): Route[] {
    const arr: Route[] = [];
    this.route.map((data) => {
      if(data.name.indexOf(inputValue) === 0){
        arr.push(data);
      }
    });
    return arr;
  }
}


